.wrapper {
  width: 160px;
}

.card {
  position: relative;
  width: 160px;
  height: 200px;
  border-radius: 10px;
  cursor: pointer;
}

.card > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.titleWrapper {
  margin-top: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.titleWrapper > p {
  color: var(--white-color);
}

.banner {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 42px;
  background-color: var(--white-color);
  display: flex;
  align-items: center;
  padding: 6px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;

  .chip {
    color: var(--white-color);
    background-color: var(--black-color);
  }
}
